export default {
	data() {
		return {
			/* 当前标签 */
			tabCur: 0,
			/* 列表 */
			cartList: [{
				type: 0
			}, {
				type: 0
			}],
		}
	},
	onReachBottom() {
		uni.showLoading({
			title: '加载中',
			mask: true
		})
		setTimeout(() => {
			uni.hideLoading()
		}, 1000);
	},
	onLoad() {
		uni.showModal({
			title: '该功能正在开发中',
			content: '敬请期待',
			showCancel:false,
			success: function(res) {
				if (res.confirm) {
						uni.navigateBack()
				}
			}
		});
	},
	methods: {
		/* 标签选择 */
		tabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.swiperCur = this.tabCur
		},
		/* 页面跳转 */
		jump2item() {
			uni.navigateTo({
				url: '/pages/item/index'
			})
		}
	}
}
